import {h, API, start, Input, Textarea, Range, Rating, Select, Radio, Checkbox, Switch, ColorPicker, UpdateButton, UpdateIconButton, SettleButton, Dialog, Alert, Drawer, Spinner} from './shoelace'
import {makeRestRepository} from '../../src/core/rest'
import {submit, reset} from '../../src/core/updates'
import {StartParameter} from '../../src/core/framework'

const schema = {
  type: 'object', 
  properties: {
    name: {type: 'string', notEmpty:true}, 
    content: {type: 'string', notEmpty:true}, 
    hours: {type: 'integer'}, 
    rating: {type: 'number'}, 
    position: {type: 'string', notEmpty:true, enum:['option-1', 'option-2']}, 
    sex: {type: 'string', notEmpty:true, enum:['male', 'female']}, 
    agree: {type: 'boolean'}, 
    home: {type: 'boolean'}, 
    bgcolor: {type: 'string', notEmpty:true}, 
    dummy: {type:'object', properties:{i:{type:'integer'} }} 
  }
}

const data = {
  name: '', 
  content: '', 
  hours: null, 
  rating: null, 
  position: '', 
  sex: '', 
  agree: false, 
  home: false, 
  bgcolor: '', 
  dummy: {i:1}
}

const updates = {
  successSubmit: submit(makeRestRepository('http://localhost:3000/things')), 
  failureSubmit: submit(makeRestRepository('http://localhost:3000/false')), 
  reset
}

// TODO: Menu

const messageProps = {style:{color:"#FF0000"}}
const callOnUpdate = (x) => onUpdate(x)
const view = (store) => {
  return (
    <sl-form novalidate>
      <Input path="/name" label="" messageProps={messageProps}>
        <sl-icon name="house" slot="prefix"></sl-icon>
        <div slot="help-text">Your Name.</div>
      </Input>
      <Textarea path="/content" label="" messageProps={messageProps}>
        <div slot="help-text"></div>
      </Textarea>
      <Range path="/hours" label="" messageProps={messageProps} min="4" max="12" step="1">
        <div slot="help-text"></div>
      </Range>
      <Rating path="/rating" key="rating" />
      <Select path="/position" label="" messageProps={messageProps}>
        <sl-menu-item value="option-1">Option 1</sl-menu-item>
        <sl-menu-item value="option-2">Option 2</sl-menu-item>
        <sl-menu-item value="option-3">Invalid Option 3</sl-menu-item>
        <div slot="help-text"></div>
      </Select>
      <sl-radio-group label="">
        <Radio path="/sex" value="male"></Radio>
        <Radio path="/sex" value="female"></Radio>
        <Radio path="/sex" value="other"></Radio>
      </sl-radio-group>
      <div><Checkbox path="/agree"></Checkbox></div>
      <div><Switch path="/home"></Switch></div>
      <div><ColorPicker path="/bgcolor" /></div>
      <div>
        <UpdateButton name="loading" update="successSubmit" context={["add", {path:"/dummy", errorSelector:":invalid", method:"POST"}]}>()</UpdateButton>
        <UpdateButton name="loading" update="failureSubmit" context={["add", {path:"/dummy", errorSelector:":invalid", method:"POST"}]}>()</UpdateButton>
        <UpdateIconButton name="x-circle" update="reset" context={[data, {}]} />
        <UpdateButton update="toggleSwitch" context={["drawer"]}>Open Drawer</UpdateButton>
        <UpdateButton update="openProgress" context={['spinner', null]}>Show Progress</UpdateButton>
        <UpdateButton update="closeProgress" context={['spinner']}>Hide Progress</UpdateButton>
      </div>
      <Dialog key="confirm" name="confirm" label="" message="" />
      <Alert key="success" name="success" type="success" message="" closable sl-duration={5000} onUpdate={callOnUpdate} />
      <Alert key="failure" name="failure" type="danger" message="{message}" closable onUpdate={callOnUpdate} />
      <Drawer key="drawer" name="drawer" onUpdate={callOnUpdate}>
        
      </Drawer>
      <Spinner key="spinner" name="spinner" />
    </sl-form>
  )
}

console.log(updates)
const containerEl = document.getElementById('app')
const {onUpdate} = start({schema, data, view, containerEl, updates})